}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#carousel-container {
  max-height: 20vh;
}

.slide {
  height: 65vh;
  width: 100%;
}

#bright-content {
  height: 2vh;
  width: 20%;
  margin-left: 40%;
  padding-top: 5%;
  margin-top: 5%;
}
#bright-content-header {
  font-size: 50px;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: 3%;
  padding-top: 2%;
  padding-left: 10%;
  padding-right: 10%;
}
.header-first {
  background-color: rgba(0, 0, 0, 0.7);
  color: rgb(157, 219, 11);
  box-shadow: 5px 4px 4px 1px rgba(157, 219, 11, 0.7);
}

.header-second {
  background-color: rgba(0, 0, 0, 0.7);
  color: rgba(11, 192, 219, 1);
  box-shadow: 5px 4px 4px 1px rgba(11, 192, 219, 1);
}

.header-third {
